body {
  margin: 0;
  background: rgba(17, 24, 33);
  color: white;
  font-family: "Roboto";
  /* height: 100%; */
}
.app {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}
.side-line {
  margin-left: 320px;
}
@media only screen and (max-width: 768px) {
  .app {
    flex-direction: column;
  }
  .side-line {
    margin-left: 0;
  }
}
.primary-btn {
  display: flex;
  flex-direction: row;
  border: none;
  color: rgba(255, 255, 255);
  cursor: pointer;
  width: 160px;
  height: 40px;
  background-color: rgb(108, 84, 162);
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  font-weight: 300;
  transition: all 0.5s;
}
.primary-btn > span {
  margin-left: 10px;
  font-weight: bold;
  font-style: normal;
  font-size: 15px;
}
.primary-btn:hover {
  background-color: rgba(108, 84, 162, 0.8);
  transition: all 0.5s;
}
.secondary-btn {
  display: flex;
  flex-direction: row;
  border: none;
  color: rgba(255, 255, 255);
  cursor: pointer;
  width: 160px;
  height: 40px;
  background-color: rgb(48, 68, 91, 0.6);
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  font-weight: 300;
  transition: all 0.5s;
}
.secondary-btn > span {
  margin-left: 10px;
  font-weight: bold;
  font-style: normal;
  font-size: 15px;
}
.secondary-btn:hover {
  background-color: rgb(48, 68, 91, 1);
  transition: all 0.5s;
}
